<template>
  <div id="app">
    <!-- <v-overlay :value="loading">
      <v-progress-circular
        indeterminate
        size="64"
      ></v-progress-circular>
    </v-overlay> -->

    <!-- loading skeleton Start -->
    <v-col v-show="loading == true">
      <v-sheet
        height="565"
        class="pa-2 mb-n5"
        max-width="6000"
        :color="$vuetify.theme.dark ? 'grey darken-4' : 'grey lighten-5'"
      >
        <div>
          <!-- Heading -->
          <v-card-actions>
            <v-skeleton-loader
              width="400"
              type="heading"
            ></v-skeleton-loader>
            <v-spacer></v-spacer>
            <!-- Button -->
            <v-skeleton-loader
              type="card"
              width="80"
              height="35"
            ></v-skeleton-loader> 
            <v-skeleton-loader
              type="card"
              width="80"
              height="35"
              class="ml-2"
            ></v-skeleton-loader>
          </v-card-actions>
        </div>
        
        <div>
          <!-- Body 1 -->
          <v-skeleton-loader
            tile
            height="50"
            type="image"
            class="mt-1"
          ></v-skeleton-loader>
        </div>

        <div>
          <!-- Search -->
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-skeleton-loader
              type="image"
              width="250"
              height="20"
              class="rounded-xl mx-n1 mt-1 mb-1"
            ></v-skeleton-loader>
          </v-card-actions>
        </div>

        <div>
          <!-- Body 2 -->
          <v-skeleton-loader
            tile
            height="200"
            type="image"
          ></v-skeleton-loader>
          <v-skeleton-loader
            tile
            height="132"
            type="image"
            class="mb-3"
          ></v-skeleton-loader>
        </div>

        <div>
          <!-- page -->
          <v-card-actions>
            <v-skeleton-loader
              width="800"
              type="heading"
              class="mt-1 mx-n2"
            ></v-skeleton-loader>

            <v-spacer></v-spacer>

            <v-skeleton-loader
              type="image"
              width="170"
              height="20"
              class="rounded-xl mx-n1"
            ></v-skeleton-loader>
          </v-card-actions>
        </div>
      </v-sheet>
    </v-col>
    <!-- loading skeleton End -->
   
    <!-- Alert Tidak Punya Akses Halaman -->
    <v-col v-show="this.valid == false">
      <v-alert text type="error">
        Kamu Tidak punya Akses Ke halaman "Karyawan"
      </v-alert>
    </v-col>

    <!-- Alert Data Tersimpan -->
    <v-alert style="position:fixed;z-index:2;right:20px;"
      dark
      dismissible
      elevation="4"
      color="success"
      v-model="Alert"
      class="rounded-lg"
      icon="mdi-check-bold"
      transition="slide-x-transition"
    >
      <v-icon class="mr-1">check-circle</v-icon>
      Data Berhasil Disimpan
    </v-alert>

    <!-- Alert Data Berhasil Terubah -->
    <v-alert style="position:fixed;z-index:2;right:20px;"
      dark
      dismissible
      elevation="4"
      color="success"
      class="rounded-lg"
      icon="mdi-check-bold"
      v-model="AlertBerhasilTerubah"
      transition="slide-x-transition"
    >
      Data Berhasil Diubah
    </v-alert>

    <!-- Alert Data Sudah Terhapus -->
    <v-alert style="position:fixed;z-index:2;right:20px;"
      dark
      dismissible
      color="error"
      elevation="4"
      class="rounded-lg"
      icon="mdi-delete"
      v-model="AlertDataTerhapus"
      transition="slide-x-transition"
    >
      Data Berhasil Dihapus
    </v-alert>

    <!-- ---------------------------------------------------- -->

    <v-container fluid v-show="this.valid == true">
      <v-sheet tile outlined class="elevation-3 pa-2" v-show="loading == false">
        <v-toolbar
          flat
          fixed
          outlined
          height="55"
          color="dark"
          v-resize="onResize"
        >
          <v-toolbar-title>
            Data Karyawan
          </v-toolbar-title>
          <v-divider inset vertical class="ml-2"></v-divider>
          <v-spacer></v-spacer>

          <!-- Menu Export Data -->
          <div class="text-center mx-2">
            <v-menu
              v-model="MenuExport"
              :close-on-content-click="false"
              :nudge-width="1"
              offset-y
            >
              <template v-slot:activator="{ on, attrs }">
                <!-- Btn Export Mode Biasa -->
                <v-btn
                  @click="MenuExport = !MenuExport"
                  class="text-capitalize"
                  v-show="mobile == false"
                  v-bind="attrs"
                  v-on="on"
                  outlined
                  text
                >
                  <v-icon class="ml-n2 mr-1">mdi-table-arrow-right</v-icon>
                  <span class="subtitle-1">Export Data</span>
                  <v-icon class="ml-1 mr-n3">{{ MenuExport ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
                </v-btn>
              </template>

              <v-card outlined height="100" class="pa-2" v-show="MenuExport">
                <!-- PDF -->
                <div class="mb-2">
                  <v-btn
                    text
                    block
                    outlined
                    @click.native="PdfExport"
                    class="text-capitalize subtitle-1"
                  >
                    Pdf
                    <v-icon right>mdi-adobe-acrobat</v-icon>
                  </v-btn>
                </div>

                <!-- EXCEL -->
                <div>
                  <v-btn
                    text
                    block
                    outlined                       
                    @click.native="excelExport"
                    class="text-capitalize subtitle-1"
                  >
                    Excel
                    <v-icon right>mdi-microsoft-excel</v-icon>
                  </v-btn>
                </div>
              </v-card>
            </v-menu>

            <!-- Btn Export Dialog Mode Mobile -->
            <v-dialog
              v-model="DialogPilihExport"
              max-width="200"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  v-show="mobile == true"
                  v-bind="attrs"
                  v-on="on"
                  outlined
                  small
                  text
                  fab
                >
                  <v-icon>mdi-table-arrow-right</v-icon>
                </v-btn>
              </template>
              <v-card class="rounded-lg">
                <v-toolbar dense>
                  <v-layout justify-center>
                    <v-toolbar-title>
                      Pilih Export File
                    </v-toolbar-title>
                  </v-layout>
                </v-toolbar>
                <v-card class="pa-2">
                  <v-btn text block outlined class="text-capitalize subtitle-1 mb-2" @click.native="PdfExport">
                    Pdf
                    <v-icon right>mdi-adobe-acrobat</v-icon>
                  </v-btn>

                  <v-btn text block outlined class="text-capitalize subtitle-1" @click.native="excelExport">
                    Excel
                    <v-icon right>mdi-microsoft-excel</v-icon>
                  </v-btn>
                </v-card>
              </v-card>
            </v-dialog>
          </div>

          <!-- Dialog Tambah Karyawan -->
          <v-dialog
            v-model="DialogTambahKaryawan"
            max-width="700px"
            scrollable
            persistent
          >
            <template v-slot:activator="{ on, attrs }">
              <!-- Btn Tambah Mode Desktop -->
              <v-btn
                class="text-capitalize mr-n2"
                v-show="mobile == false && ValidCreate == true"
                v-bind="attrs"
                v-on="on"
                outlined
                text
              >
                <v-icon class="ml-n2 mr-1">mdi-plus-thick</v-icon>
                <span class="mr-n1 subtitle-1">Tambah</span>
              </v-btn>

              <!-- Btn Tambah Mode Mobile -->
              <v-btn 
                class="mr-n2"
                v-show="mobile == true && ValidCreate == true"
                v-bind="attrs"
                v-on="on"
                outlined
                small
                text
                fab
              >
                <v-icon>mdi-plus-thick</v-icon>
              </v-btn>
            </template>
            <v-card outlined class="rounded-lg">
              <v-toolbar flat height="68">
                <v-toolbar-title>
                  <div class="text-truncate"><span class="text-h5">{{ formTitleKaryawan }}</span></div>
                  <div class="mt-n1 caption text-truncate"><span v-show="showData == false" v-text="TampilNama"></span></div>
                </v-toolbar-title>
                <v-spacer></v-spacer>
                <!-- Button Menu Tampilan Buka Semua Inputan Atau Tutup -->
                <v-menu :close-on-content-click="closeOnContentClick">
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      icon
                      small
                      v-bind="attrs"
                      v-on="on"
                    >
                      <v-icon>mdi-dots-vertical</v-icon>
                    </v-btn>
                  </template>
                  <v-card class="rounded-lg pa-2">
                    <v-list class="mt-n2 mb-n2">
                      <v-btn text block small outlined rounded class="text-capitalize" @click="ShowAll">Tampilkan Semua</v-btn>
                    </v-list>
                    <v-list class="mb-n2">
                      <v-btn text block small outlined rounded class="text-capitalize" @click="CloseAll">Tutup Semua</v-btn>
                    </v-list>                      
                  </v-card>
                </v-menu>

                <v-btn
                  icon
                  @click="KeluarDialogKaryawan"
                >
                  <v-icon>mdi-window-close</v-icon>
                </v-btn>
              </v-toolbar>
              <v-divider></v-divider>
              
              <v-card-text>
                <div class="mr-n3 ml-n3 mt-3">
                  <v-sheet outlined class="pa-2">
                    <!-- Data -->
                    <div>
                      <v-sheet outlined>
                        <a>
                          <v-hover
                            v-slot="{ hover }"
                          >
                            <v-sheet
                              @click="showData = !showData"
                              :dark="hover ? true : false"
                              :color="hover ? 'grey darken-2' : '$vuetify.theme.dark'"
                              :class="hover == { 'on-hover': hover } == $vuetify.theme.dark ? 'grey lighten-4' : '$vuetify.theme.dark'"
                            >
                              <v-card-actions>
                                <v-icon left>mdi-account-details</v-icon>
                                <span class="subtitle-1 font-weight-medium">Data</span>
                                <v-spacer></v-spacer>
                                <v-btn text small outlined class="text-capitalize">{{ showData ? 'Tutup' : 'Tampilkan' }}</v-btn>
                              </v-card-actions>
                            </v-sheet>
                          </v-hover>
                        </a>

                        <v-expand-transition transition="false">
                          <div v-show="showData">
                            <v-divider></v-divider>
                            <div class="pa-5">
                              <v-row>
                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="Nama"
                                    color="dark"
                                    v-model="editedItem.Nama"
                                    hint="Wajib Diisi." 
                                    :rules="[value => !!value || 'Tidak Boleh Kosong']"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="NRK"
                                    color="dark"
                                    v-model="editedItem.NRK"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="POH"
                                    color="dark"
                                    v-model="editedItem.POH"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="Lokasi"
                                    color="dark"
                                    v-model="editedItem.Lokasi"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-autocomplete
                                    dense
                                    clearable
                                    label="Cabang"
                                    color="dark"
                                    :items="DataCabang"
                                    item-text="namacabang"
                                    item-value="namacabang"
                                    v-model="editedItem.Cabang"
                                  ></v-autocomplete>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-dialog
                                    v-model="MenuTglMasuk"
                                    :return-value.sync="date"
                                    width="290px"
                                  >
                                    <template v-slot:activator="{ on, attrs }">
                                      <v-text-field
                                        v-model="editedItem.TglMasuk"
                                        label="Tgl Masuk"
                                        prepend-icon="mdi-calendar"
                                        color="dark"
                                        readonly
                                        dense
                                        v-bind="attrs"
                                        v-on="on"
                                      ></v-text-field>
                                    </template>
                                    <v-date-picker
                                      v-model="editedItem.TglMasuk"
                                      @input="MenuTglMasuk = false"
                                      year-icon="mdi-calendar-blank"
                                      color="red darken-4"
                                      class="rounded-lg"
                                      locale="id"
                                      scrollable
                                    ></v-date-picker>
                                  </v-dialog>
                                </v-col>
                                
                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="Tempat Lahir"
                                    color="dark"
                                    v-model="editedItem.TempatLahir"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-dialog
                                    v-model="MenuTglLahir"
                                    :return-value.sync="date"
                                    width="290px"
                                  >
                                    <template v-slot:activator="{ on, attrs }">
                                      <v-text-field
                                        v-model="editedItem.TglLahir"
                                        label="Tgl Lahir"
                                        prepend-icon="mdi-calendar"
                                        color="dark"
                                        readonly
                                        dense
                                        v-bind="attrs"
                                        v-on="on"
                                      ></v-text-field>
                                    </template>
                                    <v-date-picker
                                      v-model="editedItem.TglLahir"
                                      @input="MenuTglLahir = false"
                                      year-icon="mdi-calendar-blank"
                                      color="red darken-4"
                                      class="rounded-lg"
                                      :max="new Date().toISOString().substr(0, 10)"
                                      min="1950-01-01"
                                      locale="id"
                                      ref="picker"
                                    ></v-date-picker>
                                  </v-dialog>
                                </v-col>

                                <v-col cols="12" sm="6" md="12" lg="12">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="Alamat KTP"
                                    color="dark"
                                    v-model="editedItem.AlamatKTP"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="12" lg="12">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="Alamat Sekarang"
                                    color="dark"
                                    v-model="editedItem.AlamatSekarang"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="No. Tlp"
                                    color="dark"
                                    v-model="editedItem.NoTlp"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="Jenis Kelamin"
                                    color="dark"
                                    v-model="editedItem.JenisKelamin"
                                  ></v-text-field>
                                  <!-- <v-combobox
                                    dense
                                    clearable
                                    :items="['Laki-Laki','Perempuan']"
                                    label="Jenis Kelamin"
                                    color="dark"
                                    v-model="editedItem.JenisKelamin"
                                  ></v-combobox> -->
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="Pendidikan Terakhir"
                                    color="dark"
                                    v-model="editedItem.PendidikanTerakhir"
                                  ></v-text-field>
                                  <!-- <v-combobox
                                    dense
                                    clearable
                                    label="Pendidikan Terakhir"
                                    color="dark"
                                    :items="['SD/Sederajat','SLTP/Sederajat','SLTA/Sederjat','Diploma I/I','Akademi/Diploma III','Strata I','Strata II','Strata III']"
                                    v-model="editedItem.Pendidikan_Terakhir"
                                  ></v-combobox> -->
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="Jurusan"
                                    color="dark"
                                    v-model="editedItem.Jurusan"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-combobox
                                    dense
                                    clearable
                                    :items="['Islam','Protestan','Katolik','Hindu','Buddha','Khonghucu']"
                                    label="Agama"
                                    color="dark"
                                    v-model="editedItem.Agama"
                                  ></v-combobox>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="Suku"
                                    color="dark"
                                    v-model="editedItem.Suku"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="No. Rekening Tabungan"
                                    color="dark"
                                    v-model="editedItem.NoRekTabungan"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="No. Kartu BPJS TK"
                                    color="dark"
                                    v-model="editedItem.NoKartuBPJSTK"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="No. Kartu BPJS KES"
                                    color="dark"
                                    v-model="editedItem.NoKartuBPJSKes"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="No. KTP"
                                    color="dark"
                                    v-model="editedItem.NoKTP"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="No. NPWP"
                                    color="dark"
                                    v-model="editedItem.NoNPWP"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-combobox
                                    dense
                                    clearable
                                    label="Golongan Darah"
                                    color="dark"
                                    :items="['A','B','AB','O']"
                                    v-model="editedItem.GolDarah"
                                  ></v-combobox>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="Status Perkawinan"
                                    color="dark"
                                    v-model="editedItem.StatusPerkawinan"
                                  ></v-text-field>
                                  <!-- <v-combobox
                                    dense
                                    clearable
                                    label="Status"
                                    color="dark"
                                    :items="['Belum Menikah','Menikah']"
                                    v-model="editedItem.StatusPerkawinan"
                                  ></v-combobox> -->
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="Status Kerja"
                                    color="dark"
                                    v-model="editedItem.StatusKerja"
                                  ></v-text-field>
                                  <!-- <v-combobox
                                    dense
                                    clearable
                                    label="Status Kerja"
                                    color="dark"
                                    :items="['Aktif','Tidak Aktif']"
                                    v-model="editedItem.StatusKerja"
                                  ></v-combobox> -->
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="Ukuran Baju"
                                    color="dark"
                                    v-model="editedItem.UkuranBaju"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="Ukuran Sepatu"
                                    color="dark"
                                    v-model="editedItem.UkuranSepatu"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="PHK"
                                    color="dark"
                                    v-model="editedItem.PHK"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-dialog
                                    v-model="MenuTglPHK"
                                    :return-value.sync="date"
                                    width="290px"
                                  >
                                    <template v-slot:activator="{ on, attrs }">
                                      <v-text-field
                                        v-model="editedItem.TglPHK"
                                        label="Tanggal PHK"
                                        prepend-icon="mdi-calendar"
                                        color="dark"
                                        readonly
                                        dense
                                        v-bind="attrs"
                                        v-on="on"
                                      ></v-text-field>
                                    </template>
                                    <v-date-picker
                                      v-model="editedItem.TglPHK"
                                      @input="MenuTglPHK = false"
                                      year-icon="mdi-calendar-blank"
                                      color="red darken-4"
                                      class="rounded-lg"
                                      locale="id"
                                    ></v-date-picker>
                                  </v-dialog>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="Keterangan"
                                    color="dark"
                                    v-model="editedItem.Keterangan"
                                  ></v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    clearable
                                    label="Nama Istri Suami"
                                    color="dark"
                                    v-model="editedItem.NamaIstriSuami"
                                  >
                                  </v-text-field>
                                </v-col>

                                <v-col cols="12" sm="6" md="6" lg="6">
                                  <v-text-field
                                    dense
                                    prefix="Anak"
                                    color="dark"
                                    readonly
                                    v-model="editedItem.Anak"
                                  >
                                    <!-- Dialog Anak -->
                                    <template v-slot:append-outer>
                                      <v-dialog
                                        v-model="DialogAnak"
                                        max-width="750px"
                                        scrollable
                                        hide-overlay
                                      >
                                        <template v-slot:activator="{on, attrs}">
                                          <v-btn
                                            dark
                                            x-small
                                            height="25"
                                            v-bind="attrs"
                                            v-on="on"
                                            color="red darken-4"
                                          >
